.noDec{
  text-decoration: none!important;
  color: black;
}
.white-div{
  flex: 1 1;
  box-shadow: 0 0 10px white;
}
.gray-div{
  background: #f0eeed;
  flex: 1 1;
  box-shadow: 0 0 10px #a0aaa9;
}
.background{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: calc(40vw + 40vh);
    position:absolute;
    z-index: -2;
}
.mobile-background{
    display: none;
}
.leftPanel{
  margin-left: 5%;
  flex: 0.8 1;
  z-index: 1;
}
.header{
  margin-bottom: 18%;
  font-size: calc(2vw + 2vh);
  font-weight: 700;
  text-shadow: 1px 1px 1px
}

.sideNavOption{
  font-size: calc(1.2vw + 1.2vh);
  font-weight: 700;
  line-height: 1.5;
  cursor: pointer;
  text-shadow: 1px 1px 1px
}
.sideNav{
}
.leftPanelShoe{
  width: 70%;
  transform: scaleX(-1);
  margin-top: 5%;
  margin-left: -20%;
}

.group-info-links{
  display:flex;
  flex-wrap: wrap;
}
.left{
  flex: 10 1;
}
.right{
  flex: 0 1;
}
.group {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
}

.dot{
  margin: 2% 0;
  display: block;
  cursor: pointer;
}
.miniPanel {
  margin-top: 13%;
  flex: 1.2 1;
  z-index:1
}
.mainImage{
  position: absolute;
  top:-10%;
  left: 16%;
  width: 50%;
  height: auto;
  transform: rotate(-40deg);

}
.info {
  margin-top: 2.5%;
  flex: 1 1;
}
.groupCart {
  width:100%;
  height: auto;
  margin-top: 3%;
  margin-right: 5%;
  display: flex;
  justify-content: flex-end;
}
.cart{
  vertical-align: middle;
  font-size: 34px;
  font-weight: 800;
  display: inline;
}
.cartIcon{
  vertical-align: middle;
  margin-left: 5%;
  height: 50px;
  width: auto;
  text-align: right;
}
.shoeName{
  font-size: calc(2.8vw + 2.8vh);
  font-weight: 900;
  line-height: 1;
  color: transparent;
  background-clip: text;
}
.gradient1{
  background-image: linear-gradient(to right, #000, #333 25%,  #777 85%);
  opacity:1;
  transition: 1s;
}
.gradient2{

  background-image: linear-gradient(to right, #00a, #00f 25%,  grey 85%);
  opacity:0;
  display: none;
  transition: 1s;
}
.gradient3{
  background-image: linear-gradient(to right, #000, #bf0d0d 25%,  orange 85%);
  opacity:0;
  display: none;
  transition: 1s;
}

.img1{
  position: absolute;
  opacity:1;
  transition: 1s;
  filter: drop-shadow(0 3vw 0.5vw rgba(0, 0, 0, 0.3));
}
.img2{
  position: absolute;
  transition: 1s;
  opacity: 0;
  filter: drop-shadow(0 3vw 0.5vw rgba(0, 0, 170, 0.3));

}
.img3{
  transition: 1s;
  opacity: 0;
  filter: drop-shadow(0 3vw 0.5vw rgba(191, 13, 13, 0.3));
}

.price{
  font-weight: 900;
  line-height: 2;
  font-size: calc(1.8vw + 1.8vh);
  text-shadow: 1px 1px 1px;
}
.desc{
  width: 90%;
  font-size: calc(0.65vw + 0.65vh);
  line-height: 2;
  text-shadow: 1px 1px 1px;
  color: dimgray;
}
.addToCart{
  background-color: black;
  color: white;
  text-align: center;
  margin-top: 8%;
  width: 90%;
  height: 15%;
  font-size: calc(1vw + 1vh);
  font-weight: 900;
}
.group-buttons{
  margin-top: 20%;
  width: 90%;
  display: flex;
  flex-wrap: wrap;
}
.arrowLeft{
  width: 50%;
  height: auto;
  flex: 1 1;
  transform: rotate(-180deg);
  cursor: pointer;
}
.arrowRight{
  width: 50%;
  height: auto;
  flex: 1 1;
  cursor: pointer;
}
.linksMenu{
  flex: 0.01 1;
  margin-top: 200%;
}
.link{
  font-size: calc(1vw + 1vh);
  margin-top:100%;
  font-weight: 900;
  transform: rotate(-90deg);
}
.noSelect{
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
}

.top-panel{
  display: flex;
  justify-content: space-between;
}
.nikeLogo{
    margin-top: 3%;
  margin-left: 5%;
  width: 12%;
  height: auto;
}
.dropdown__icon{
  display: none;
  position: absolute;
  margin-top: 2.5%;
  font-weight: 800;
  left: 30%;
  width:30%;
  font-size: 20px;
  z-index: 3;
}
.dropdown__icon:hover{
  cursor: pointer;
}
.dropdown__menu{
  overflow-x: hidden;
  z-index: 2;
  position: fixed;
  height: 100%;
  width: 0;
  top: 0;
  left: 0;
  background: linear-gradient(to right, #9bcfdc, #b8d6e0, #d0d8da);
  transition: 0.4s ease-out;
  box-shadow: 2px 2px 2px #b8d6e0;
  border-radius: 10px;
}
.dropdown__option{
  border-radius: 5px;
  opacity: 1;
  text-align: center;
  margin: 5%;
  color: black;
  background-color: white;
  font-size: 20px;
  cursor: pointer;
  border: solid 1px black;
}
.dropdown__option:hover{
  font-weight: bold;
}
.dropdown__option--first{
  font-weight: bold;
}
.category{
  text-align: center;
  font-size: 32px;
  font-weight: 700;
  display: none;
}
.alert{
  z-index: 4;
  position: fixed;
  width: calc(20vw + 20vh);
  height: calc(3vw+ 3vh);
  font-size: calc(1.5vw + 1.5vh);
  font-weight:600;
  margin: auto;
  left: 0;
  right: 0;
  text-align: center;
  background-color: #333;
  color: white;
  border-radius: 5px;
  transition: opacity 5s ease-in;
}
.alert__button{
  background-color: #333;
  border: 0;
  color: grey;
}

@media (max-width: 600px){
  .category{
    display:block
  }
    .mobile-background{
        z-index: -1;
        max-width: 100%;
        height: 110%;
        display: block;
        position: fixed;
        left:0;
        top:0;
        opacity: 0.3
    }
    .group-info-links{
        display: block;
    }
  .miniPanel{
    display: none;
  }
  .leftPanel{
    display: none;
  }
  .background{
    display: none;
  }

  .nikeLogo{
      width: 25%;
  }
  .info{
      margin-top: 60%;
      text-align: center;
  }
  .desc{
      margin-left: 15%;
      margin-right: 15%;
      width: 70%;
      font-size: 14px;
  }
  .addToCart{
      height: 50px;
      width: 70%;
      margin-top: 3%;
      margin-left: 15%;
      margin-right: 15%;
      font-size: 14px;
  }
  .group-buttons{
      width: 70%;
      margin: 0 15%;
  }
  .linksMenu{
      margin-top: 0;
      position: absolute;
      right:0;
      bottom: 4%;
  }
  .mainImage{
      top: 10%;
      left: 5%;
      width: 75%;
      height: auto;
  }
  .shoeName{
      font-size: 36px;
  }
  .price{
      font-size: 28px;
      line-height: 1;
  }
  .link{
      font-size: 12px;
  }
  .dropdown__icon{
    display:block;
    margin-top: 2.5%;
    z-index: 1;
  }
  .dropdown__icon:hover{
    cursor: pointer;
  }
  .cartIcon{
    height: 30px;
  }
  .cart{
    font-size: 20px;
  }
}
